<template>
    <v-card :class="customClass">

    <slot name="img"></slot>

    <div class="d-flex justify-center" :style="'height:'+circleSize/2 +'px;'">
      <v-avatar color="white" :size="circleSize+10" :style="'top:' + -((circleSize/2)+4) + 'px ;'">
        <v-avatar :color="circleColor" :size="circleSize">
          <v-icon v-if="!isAvatarIcon" color="white" :size="circleSize/2">{{icon}}</v-icon>
          <slot v-else name="avatarImg"></slot>
        </v-avatar>
      </v-avatar>
    </div>

    <slot></slot>

    <slot name="footer"></slot>

    </v-card>
</template>

<script>
export default {
  name: 'card-full',
  props: {
    customClass: {
      type: String,
      default: ''
    },
    circleColor: {
      type: String,
      default: 'secondary'
    },
    circleSize: {
      type: Number,
      default: 90
    },
    icon: {
      type: String,
      default: 'mdi-account-tie'
    },
    isAvatarIcon: {
      type: Boolean,
      default: false
    }
  },
  data: () => ({
  })
}
</script>
